import React, { useState, useCallback } from 'react'
import Waypoint from 'react-waypoint';

export default function RevealSection({ children }) {
  const [isVisible, setIsVisible] = useState(false)
  const handlePositionChange = useCallback(({ currentPosition }) => {
    if (currentPosition === Waypoint.above || currentPosition === Waypoint.inside) {
      setIsVisible(true)
    }
  }, [setIsVisible])

  return (
    <Waypoint onPositionChange={handlePositionChange} bottomOffset='300px'>
      {/* {isVisible ? (
        <div key="visible" style={{ opacity: '0', animation: 'fadeIn 1s forwards' }}>
          {children}
        </div>
      ) : (
        <div key="hidden" style={{ opacity: '0' }}>
          {children}
        </div>
      )} */}
      {isVisible && children}
    </Waypoint>
  )
}
